@font-face {
  font-family: ConsulFont;
  src: url("Consul.8f38edf0.ttf");
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #1e201e;
  background-color: #f4f4f4;
  font-family: Arial, sans-serif;
}

header {
  color: #fff;
  background-color: #1e201e;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 15px 0;
  display: flex;
}

.logo {
  color: red;
  padding: 50px 0 0 30px;
  font-family: ConsulFont;
  font-size: 65px;
  font-weight: bold;
}

nav {
  width: 100%;
}

nav ul {
  padding-top: 30px;
  padding-left: 500px;
  list-style: none;
}

nav ul li {
  margin: 0 15px;
  display: inline;
}

nav ul li a {
  color: #fff;
  font-size: 18px;
  text-decoration: none;
}

nav ul li a.active {
  font-weight: bold;
}

nav ul li:hover {
  cursor: pointer;
