  height: 30px;
  margin-left: 10px;
  padding: 5px;
  font-size: 14px;
}

#search-Btn:hover {
  cursor: pointer;
  scale: 1.05;
}

.headline-section {
  background-color: #fff;
  width: 100%;
  margin-top: 20px;
  transition: all .5s;
  position: relative;
}

.headline-image-container {
  width: 100%;
  height: auto;
  position: relative;
}

.headline-section:hover {
  box-shadow: 5px 5px 5px #aaa;
}

#headline-link {
  color: #000;
  text-decoration: none;
}

.headline-overlay {
  color: #fff;
  text-align: center;
  background-image: linear-gradient(to right, #000000bf, #0000);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 100%;
  padding: 20px;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

#headline-image {
  width: 100%;
  height: auto;
}

.headline-overlay h1, .headline-overlay p {
  margin: 0;
  padding: 10px;
}

#headline-description {
  font-size: 18px;
  font-weight: 300;
}

article img {
  max-width: 100%;
  height: auto;
  margin: 10px 0;
}

.headline-section h1 {
  font-size: 2.5rem;
  transition: all .2s;
}

.headline-section h1:hover {
  cursor: pointer;
  color: #ffffffbf;
}

#headline-author {
  text-align: right;
  padding-right: 10px;
  top: 0;
}

.content-container {
  justify-content: space-between;
  align-items: flex-start;
  gap: 20px;
  margin: 20px;
  display: flex;
}

.main-content {
  background-color: #f4f4f4;
  flex-wrap: wrap;
  flex: 1;
  gap: 20px;
  margin-top: 20px;
  padding: 20px;
  display: flex;
}

