.opinion-section {
  background-color: #fff;
  border-radius: 5px;
  flex: 0 0 300px;
  height: auto;
  margin-top: 50px;
  padding: 20px;
  box-shadow: 2px 2px 10px #0000001a;
}

.opinion-section article {
  background-color: #f4f4f480;
  justify-content: space-between;
  margin-bottom: 10px;
  padding: 10px;
  transition: all .2s;
  display: flex;
}

.opinion-section article:hover {
  background-color: #ddd;
  scale: 1.01;
